const webhook =
    '## Webhooks\nIn this example we\'re going to create a very simple bot using [webhooks](https://discord.com/developers/docs/resources/webhook).\n\nLet\'s check the code out:\n```cpp\n#include <dpp.h> // "import" discord.cpp\n\nint main()\n{\n  // https://discordapp.com/api/webhooks/773192673122135/nl1Owjht3AxrCI2D-eovHdG\n  //                                     ^^^^^^^^^^^^^^^ ^^^^^^^^^^^^^^^^^^^^^^^ \n  //                                       webhook id         webhook token\n  dpp::Webhook wh("HOOK_ID_HERE","HOOK_TOKEN_HERE"); \n  // alternatively: const dpp::Webhook wh("FULL_HOOK_URL_HERE");\n  wh.setName("Julia");\n  wh.setAvatarUrl("https://pbs.twimg.com/media/EdOUUnLWkAA8ULI.jpg");\n	  \n  wh.send("My name is Julia"); // * Check the notes\n  \n  return 0;\n}\n```\n### Result:\n<img src="https://i.ibb.co/bJZ9QZg/image.png">\n\n### Notes\n \n * You can use the send method just like you would use `Channel::send`, but be aware: `Webhook:send` takes in an `std::vector<Embed>` as a parameter, not a single `Embed`.\n * If you don\'t set any webhook property, you can declare it as `const`\n * Check the Webhook class section to get more information on how to use it';
